body {
  margin: 0;
  font-family: Roboto, sans-serif;
  display: flex;
}

.nav {
  height: 60px;
  background-color: #fff;
}

header {
  display: flex;
  padding: 0 50px;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.logo-link {
  display: block;
  width: 40px;
  height: 40px;
}

.header-content {
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  position: relative;
  display: flex;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  white-space: nowrap;
  text-decoration: none;
  text-transform: uppercase;
  color: #000;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

li {
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active::after {
  position: absolute;
  content: '';
  width: 100%;
  bottom: 0;
  background-color: #00acdc;
  right: 0;
  height: 4px;
  border-radius: 8px;
  border-color: #00acdc;
  display: block;
}
